#app3 .square{
    margin: 10vw 0 0 10vw;
    width: 10vw;
    height: 10vw;
    border: 1px solid gray;
    border-radius: 0;
    transition: transform 1s;    
}

#app3 .square.active{
    transform: translateX(15vw);
    border-radius: 50%;
}
